
import { defineComponent, onMounted, ref } from "vue";
import { SearchBar } from "@/components/index";
import { queryCity } from "@/modules/apis/index";
import { consts } from "@/modules/consts/index";
import Navigate from "@/utils/navigate";

type LocationData = {
  lat: number;
  lng: number;
  city: string;
  steet?: string;
  address?: string;
};

interface CityData {
  type1: number;
  type2: number;
  type3: number;
  list: Array<City>;
}

interface City {
  id: number;
  name: string;
  longitude?: number;
  latitude?: number;
  children: Array<City>;
}

export default defineComponent({
  name: "Citys",
  components: {
    SearchBar,
  },
  setup() {
    const router = Navigate.get();

    const inputStr = ref<string>(consts.city.name);
    const location = ref<LocationData>({
      lat: 0,
      lng: 0,
      city: "",
    });
    const cityData = ref<CityData>({
      type1: -1,
      type2: -1,
      type3: -1,
      list: [],
    });
    const cityList = ref<Array<City>>([]);
    const cityList2 = ref<Array<City>>([]);
    const cityList3 = ref<Array<City>>([]);

    // 
    function gotoSearch() {
      router.replace("search");
    }

    // 
    function onChangeType(index: number) {
      const value = cityData.value;
      value.type1 = index;
      value.type2 = -1;
      value.type3 = -1;
      updateView();
    }

    // 
    function onChangeType2(index: number) {
      const value = cityData.value;
      value.type2 = index;
      value.type3 = -1;
      updateView();
    }

    // 
    function onChangeType3(index: number) {
      const value = cityData.value;
      value.type3 = index;
      updateView();
    }

    // 
    function updateView(): void {
      const city = cityData.value;
      const type1 = city.type1;
      const type2 = city.type2;
      const type3 = city.type3;
      const types1 = city.list || [];
      let types2: Array<City> = [];
      let types3: Array<City> = [];
      if (types1.length > 0 && type1 > -1) {
        types2 = types1[type1].children || [];

        if (types2.length > 0 && type2 > -1) {
          types3 = types2[type2].children || [];
        }
      }
      cityList.value = types1;
      cityList2.value = types2;
      cityList3.value = types3;

      let cityInfo: City = types1[type1];
      if (types3.length > 0 && type3 > -1) {
        cityInfo = types3[type3];
      } else if (types2.length > 0 && type2 > -1) {
        cityInfo = types2[type2];
      }

      if (cityInfo && cityInfo.id) {
        consts.city.id = cityInfo.id;
        consts.city.name = cityInfo.name;

        inputStr.value = cityInfo.name;
      }
    }

    function onComfirm() {
      router.back();
    }

    // 
    async function loadCity() {
      const { status, data } = await queryCity({});
      if (status) {
        cityData.value.list = data;
        updateView();
      }
    }

    // 
    onMounted(() => {
      loadCity();
    });
    return {
      inputStr,
      location,
      cityData,
      cityList,
      cityList2,
      cityList3,
      onChangeType,
      onChangeType2,
      onChangeType3,
      gotoSearch,
      onComfirm,
    };
  },
});
