
/* eslint-disable */
import { defineComponent, onMounted, onUnmounted, ref } from "vue";
import { EmptyView, ListView } from "@/components/index";
import { consts } from "@/modules/consts/index";
import { queryOrgList } from "@/modules/apis/index";
import { OrgData } from "@/modules/entity/index";
import { formatDistance } from "@/utils/index";
import Navigate from "@/utils/navigate";

import { FlatItem, ListSearchBar, FilterBar } from "./widget/index";

export default defineComponent({
  name: "OrgList",
  components: {
    FlatItem,
    FilterBar,
    ListSearchBar,
    EmptyView,
    ListView,
  },
  setup() {
    const router = Navigate.get();

    let cityName = ref<string>(consts.city.name);
    const itemList = ref<Array<any>>([]); // 
    let hasMore = true; // 
    let loading = false; // 
    let params = {
      keyWord: "",
      districtId: consts.city.id,
      sortType: "1",
      orgType: "",
      orgFeature: "",
      minCost: "",
      maxCost: "",
      userLon: consts.city.lng,
      userLat: consts.city.lat,
      pageNum: 1,
      pageSize: 10,
    };

    function refreshList() {
      hasMore = true;
      params.pageNum = 1;
      queryList();
    }

    async function queryList() {
      if (!hasMore || loading) return;

      loading = true;
      const res = await queryOrgList(params);
      if (res.status) {
        const data = res.data;
        const list = data.content || [];
        list.forEach((e: OrgData) => {
          e.distance2 = formatDistance(e.distance);
        });
        if (params.pageNum === 1) {
          itemList.value = list;
        } else {
          itemList.value = [...itemList.value, ...list];
        }

        params.pageNum += 1;
        hasMore = data.totalPages > data.number;
      }
      loading = false;
    }

    function onChangeText(str: string) {
      params.keyWord = str;
      console.log(str);
    }

    function onFilterData(data: any) {
      console.log(data);
      params.maxCost = data.maxCost;
      params.minCost = data.minCost;
      params.sortType = data.id1;
      params.orgType = data.id2;
      params.orgFeature = data.id4;
      refreshList();
    }

    function gotoDetail() {
      router.push("vrview");
    }

    function onChangeCity() {
      router.push("citys");
    }

    function onPressMap() {
      router.replace("maps");
    }

    // 
    onMounted(() => {
      refreshList();
    });

    onUnmounted(() => {});
    return {
      cityName,
      itemList,
      gotoDetail,
      onChangeCity,
      onPressMap,
      onChangeText,
      onFilterData,
      refreshList,
      queryList,
    };
  },
});
