/* eslint-disable */
import * as echarts from "echarts";
import { getJsonData } from '@/modules/apis/index'
import './HomeMap.css'

// 
const color = {
  type: 'linear',
  x: 0,
  y: 0,
  x2: 0,
  y2: 1,
  colorStops: [{
      offset: 0, color: '#C79F62' // 0% 
  }, {
      offset: 1, color: '#DBAE6A' // 100% 
  }],
  global: false //  false
}

const options: any = {
  tooltip: {
    show: true,
    alwaysShowContent: true,
    confine: true,
    enterable: true,
    transitionDuration: 2000,
    renderMode: 'html',
    className: 'echart-tooltip',
    textStyle:{
        color:'#fff',
        fontSize:10,
        height:25,
        lineHeight:25
    },
    formatter: function (obj: any) {
      const htmlTxt = obj && obj.data && obj.data.name ? `${obj.data.name}<div onclick="tooltipHref(${obj.data})"><tag></tag></div>` : '';
      return htmlTxt
    }
  },
  geo: {
    top: '80',
    zoom: 1.6,
    map: 'China',
    select: {
      label: {show: false},
      itemStyle: {color}
    },
    regions: [
      {name: '', selected: true},
      {name: '', selected: true},
      {name: '', selected: true},
      {name: '', selected: true},
      {name: '', selected: true},
      {name: '', selected: true},
    ]
  },
  series: [
    {
      top: '80',
      zoom: 1.6,
      name: 'China',
      type: 'map',
      map: 'China',
      selectedMode: 'multiple',
      itemStyle: {
        borderColor: '#AA8043',
        areaColor: 'transparent'
      },
      select: {
        label: {show: false},
        itemStyle: {color}
      },
      emphasis: {
        label: {show: false},
        itemStyle: {color}
      },
      data:[
        {name: ''},
        {name: ''},
        {name: ''},
        {name: ''},
        {name: ''},
        {name: ''},
      ]
    }
  ]
};

export function getMapOption(initTooltip: string, initArr: Array<any>): void {
  const body = document.getElementById("echarts-map");
  options.geo.regions = initArr.map(item => item.selected = true)
  options.series[0].data = initArr
  if (body) {
    const myChart = echarts.init(body);
    myChart.showLoading();
    getJsonData()
      .then((json: any) => {
        myChart.hideLoading();
        if(!json.status) {
          return;
        }
        echarts.registerMap('China', json.data)
        
        // 
        myChart.setOption(options)
        myChart.dispatchAction({
          type: 'showTip',
          seriesIndex: 0,//
          name: initTooltip
        })
    })
    .catch((err) => {
      console.log(err);
    });
  }
}
