import { createRouter, createWebHistory } from 'vue-router';

import App from '../App.vue'
import Citys from './main/Citys.vue'
import Home from './main/Home.vue'
import House3d from './main/House3d.vue'
import OrgList from './main/OrgList.vue'
import OrgMap from './main/OrgMap.vue'
import Search from './main/Search.vue'
import Notfound from './other/Notfound.vue'
// import Test from './other/Test.vue'

// const history = createWebHistory();
export const routers = createRouter({
  history: createWebHistory(),
  routes: [
    // { path: '/', component: Test },
    { path: '/', redirect: '/home', component: App },
    { path: '/citys', name: 'Citys', component: Citys },
    { path: '/home', name: 'Home', component: Home },
    { path: '/vrview', name: 'VRView', component: House3d },
    { path: '/maps', name: 'Maps', component: OrgMap },
    { path: '/list', name: 'List', component: OrgList },
    { path: '/search', name: 'Search', component: Search },
    { path: '/*', name: 'Notfound', component: Notfound },
  ]
});
