<template>
  <div class="container">
    <div
      class="tab-box"
      v-for="(item, index) in tabs"
      :key="item.name"
      @click="onTabChange(index)"
    >
      <span class="tab-text">{{ item.name }}</span>
      <img
        class="tab-ic"
        :src="`static/icon/${item.select ? 'up' : 'down'}_ic.png`"
      />
    </div>

    <!--  -->
    <div v-if="tabId === 0" class="popup">
      <div
        class="popup-box"
        v-for="t1 in tab1List"
        :key="t1.id"
        @click="onPress(1, t1.id)"
      >
        <span :class="'popup-text' + (tabData.id1 === t1.id ? '2' : '')">{{
          t1.typeName
        }}</span>
        <img
          v-if="tabData.id1 === t1.id"
          class="popup-dui"
          src="static/icon/check_ic.png"
        />
      </div>
    </div>
    <!--  -->
    <div v-if="tabId === 1" class="popup">
      <div
        class="popup-box"
        v-for="t2 in tab2List"
        :key="t2.typeName"
        @click="onPress(2, t2.id)"
      >
        <span :class="'popup-text' + (tabData.id2 === t2.id ? '2' : '')">{{
          t2.typeName
        }}</span>
        <img
          v-if="tabData.id2 === t2.id"
          class="popup-dui"
          src="static/icon/check_ic.png"
        />
      </div>
    </div>
    <!--  -->
    <div v-if="tabId === 2" class="popup">
      <div class="price-input-box">
        <div class="price-input-view">
          <input
            class="price-input"
            inputMode="decimal"
            maxLength="10"
            placeholder="/"
            v-model="tabData.minCost"
          />
        </div>
        <div class="price-line" />
        <div class="price-input-view">
          <input
            class="price-input"
            inputMode="decimal"
            maxLength="10"
            placeholder="/"
            v-model="tabData.maxCost"
          />
        </div>
      </div>
      <div class="grad">
        <div class="grad-item" v-for="t3 in tab3List" :key="t3.typeName">
          <div
            :class="'grad-text' + (tabData.id3 === t3.id ? '2' : '')"
            @click="onPress(3, t3.id)"
          >
            {{ t3.typeName }}
          </div>
        </div>
      </div>
      <div class="popup-action">
        <div class="popup-clear" @click="onCancel"></div>
        <div class="popup-commit" @click="onComfirm"></div>
      </div>
    </div>
    <!--  -->
    <div v-if="tabId === 3" class="popup">
      <div class="grad">
        <div class="grad-item" v-for="t4 in tab4List" :key="t4.typeName">
          <div
            :class="'grad-text' + (tabData.id4 === t4.id ? '2' : '')"
            @click="onPress(4, t4.id)"
          >
            {{ t4.typeName }}
          </div>
        </div>
      </div>
      <div class="popup-action">
        <div class="popup-clear" @click="onCancel"></div>
        <div class="popup-commit" @click="onComfirm"></div>
      </div>
    </div>
  </div>
  <div v-if="tabId > -1" className="popup-container" @click="onCancel" />
</template>
<script lang="ts">
/* eslint-disable */
import { defineComponent, ref } from "vue";
import { consts } from "@/modules/consts/index";

const _def = {
  id1: 1,
  id2: 0,
  id3: -1,
  id4: -1,
  minCost: "",
  maxCost: "",
};
export default defineComponent({
  name: "FilterBar",
  props: {
    onChange: Function,
  },
  setup(props) {
    const tabs = [
      { name: "" },
      { name: "" },
      { name: "" },
      { name: "" },
    ];
    const tabId = ref(-1);
    const tab1List = [
      { id: 1, typeName: "" },
      { id: 2, typeName: "" },
      { id: 3, typeName: "" },
      { id: 4, typeName: "" },
    ];
    const tab2List = consts.orgTypes;
    const tab3List = consts.costTypes;
    const tab4List = consts.orgTags;

    const tabData = ref(_def);

    function onTabChange(inx: number) {
      tabId.value = inx;
      tabData.value = _def;
    }

    function onCancel() {
      tabId.value = -1;
    }

    // 
    function onComfirm() {
      tabId.value = -1;
      const valu = tabData.value;

      let minCost = valu.minCost ? valu.minCost : 0;
      let maxCost = valu.maxCost ? valu.maxCost : 10000000;
      if (valu.minCost || valu.maxCost) {
        valu.id3 = -1;
      } else if (valu.id3 > -1) {
        tab3List.forEach((e: any) => {
          if (e.id === valu.id3) {
            const prices = e.typeName.split("-");
            minCost = prices[0];
            maxCost = prices[1];
          }
        });
      }
      const data: any = {
        id1: valu.id1,
        id2: valu.id2 < 1 ? "" : valu.id2,
        // id3: valu.id3 < 1 ? "" : valu.id3,
        id4: valu.id4 < 1 ? "" : valu.id4,
        minCost,
        maxCost,
      };
      if (props.onChange) {
        props.onChange(data);
      }
    }

    function onPress(index: number, id: number) {
      switch (index) {
        case 1:
          tabData.value.id1 = id;
          break;
        case 2:
          tabData.value.id2 = id;
          break;
        case 3:
          tabData.value.id3 = id;
          break;
        default:
          tabData.value.id4 = id;
          break;
      }
      if (index !== 3) {
        onComfirm();
      }
    }

    return {
      tabs,
      tabId,
      tabData,
      tab1List,
      tab2List,
      tab3List,
      tab4List,
      onCancel,
      onComfirm,
      onPress,
      onTabChange,
    };
  },
});
</script>
