
/* eslint-disable */
import { defineComponent, onMounted } from "vue";

export default defineComponent({
  name: "Swiper",
  props: {
    index: Number,
    count: Number,
    styles: String,
    orientation: String,
    onChange: Function,
  },
  setup(props) {
    const direction = props.orientation || "column";
    const count = props.count || 5;
    let itemSize = 100;
    let position = props.index || 0;

    function onChangePage() {
      if (props.onChange) {
        props.onChange(position);
      }
    }

    function onMoveTouch() {
      // end = e.touches[0].pageX;
      const body = document.getElementById("swiper321");
      if (body) {
        if (direction === "row") {
          srcollX(body);
        } else {
          srcollY(body);
        }
      }
    }

    function onEndTouch() {
      onChangePage();
      moveTo();
    }

    function moveTo() {
      const body = document.getElementById("swiper321");
      if (body) {
        const move = position * itemSize;
        const option: ScrollToOptions = { left: 0, top: 0, behavior: "smooth" };
        if (direction === "row") {
          option.left = move;
        } else {
          option.top = move;
        }
        body.scrollTo(option);
        // body.scrollTo(move, 0);
      }
    }

    function onScroll() {
      const body = document.getElementById("swiper321");
      if (body) {
        itemSize = Math.round(body.scrollWidth / count);
        body.scrollTo(itemSize * position, 0);
        // body.addEventListener("scroll", () => {});
        // body.addEventListener("touchstart", onStartTouch, false);
        body.addEventListener("touchmove", onMoveTouch, false);
        body.addEventListener("touchend", onEndTouch, false);
      }
    }

    function srcollY(body: HTMLElement) {
      const moveY = body.scrollTop;
      itemSize = Math.round(body.scrollHeight / count);
      position = Math.round(moveY / itemSize);
      // console.log("-------->", position);
      console.log("-------->", moveY, body.scrollHeight, body.offsetHeight);
    }

    function srcollX(body: HTMLElement) {
      const moveX = body.scrollLeft;
      itemSize = Math.round(body.scrollWidth / count);
      position = Math.round(moveX / itemSize);
      // console.log("-------->", position);
      // console.log("-------->", moveX, body.scrollWidth, body.offsetWidth);
    }
    onMounted(() => {
      onScroll();
    });
    return {
      direction,
    };
  },
});
