import axios, { AxiosRequestConfig } from 'axios';

// 
const instance = axios.create({
  baseURL: 'http://47.116.70.58:8004',
  // baseURL: '/apps', // 
  timeout: 20000, // 
  headers: { 'Content-Type': 'application/json; charset=utf-8' }
});

export type Result = {
  status?: number;
  success: boolean;
  message: string;
  data: unknown;
}

//   url: method: headers?: timeout?: baseURL?: 
export function request(options: AxiosRequestConfig): Promise<any> {
  return new Promise((resolve) => {
    console.log('<====== Request Params ======>');
    console.log(options);
    
    instance.request(options).then((response) => {
      console.log('<====== Response Body ======>');
      console.log(response);  
      const { data, status, statusText } = response;
      const message = data ? (data.message ? data.message : data) : statusText;
      if (status === 200 && data.success) {
        resolve({
          data: data.result,
          status: true,
          message
        });
      } else {
        const errInfo = parseError(data ? data.code : status)
        resolve(errInfo);
      }
    }).catch((err) => {
      console.log('<====== Request Error ======>');
      console.log(err);
      resolve({
        status: false,
        message: '',
      });
    }).finally(() => {
      // q
    });
  });
}

//   url: method: headers?: timeout?: baseURL?: 
export function fetch(options: any): Promise<any> {
  return new Promise((resolve) => {
    console.log('<====== Request Params ======>');
    console.log(options);
    
    axios.create({
      timeout: 20000, // 
      headers: { 'Content-Type': 'application/json; charset=utf-8' }
    }).request(options).then((response) => {
      console.log('<====== Response Body ======>');
      console.log(response);  
      const { data, status, statusText } = response;
      if (status === 200 && statusText === 'OK') {
        resolve({
          data: data,
          status: true
        });
      } else {
        const errInfo = parseError(data ? data.code : status)
        resolve(errInfo);
      }
    }).catch((err) => {
      console.log('<====== Request Error ======>');
      console.log(err);
      resolve({
        status: false,
        message: '',
      });
    }).finally(() => {
      // q
    });
  });
}

// 
function parseError(code: number) {
  let message = 'OK';
  switch (code) {
    case 404:
      message = '';
      break;
    case 403:
      message = '';
      break;
    case 500:
      message = '';
      break;
    default:
      message = '';
      break;
  }
  return {
    status: false,
    message,
  };
}

