
/* eslint-disable */
import { defineComponent, onMounted, onUnmounted, ref } from "vue";
import { AMapAPI, districts } from "@/lib/amap/index";
import {
  queryOrgMap,
  queryMapMarker,
  queryMarkers,
} from "@/modules/apis/index";
import { consts } from "@/modules/consts/index";
import { OrgData } from "@/modules/entity/index";
import Navigate from "@/utils/navigate";
import { formatDistance } from "@/utils";
import { FlatItem, MapSearchBar, FilterBar } from "./widget/index";

export default defineComponent({
  name: "OrgMap",
  components: {
    FlatItem,
    FilterBar,
    MapSearchBar,
  },
  setup() {
    const router = Navigate.get();

    const markerData = ref<any>({});
    const showFlat = ref(false);
    let markerList: Array<OrgData> = [];
    let amap: AMapAPI;

    const params = {
      keyWord: "",
      districtId: consts.city.id,
      sortType: "1",
      orgType: "",
      orgFeature: "",
      minCost: "",
      maxCost: "",
      lon: consts.city.lng,
      lat: consts.city.lat,
      appId: Date.now(),
    };
    let marParams = {
      centreLon: 0,
      centreLat: 0,
      appId: params.appId,
      distance: 60,
    };

    // 
    async function updateMap(data: any) {
      marParams = {
        centreLon: data.lng,
        centreLat: data.lat,
        appId: params.appId,
        distance: districts[data.zoom] * 10,
      };
      // marker
      const res2 = await queryMapMarker(marParams);
      const markers = res2.data;
      amap.addMarkers(markers);

      // marker
      if (data.zoom > 10) {
        const res3 = await queryMarkers({
          userLon: data.lng,
          userLat: data.lat,
          ids: markers.map((e: any) => e.id),
        });
        const list = res3.data || [];
        list.forEach((e: OrgData) => {
          e.distance2 = formatDistance(e.distance);
        });
        markerList = list;
      }
    }

    function pressMarker(num: number, index: any) {
      if (num > 0) {
        markerData.value = markerList[index];
        if(!markerData.value.orgName) {
          return;
        }
      }
      showFlat.value = num === 1;
    }

    function gotoDetail() {
      router.push("vrview");
    }

    function onPressList() {
      router.replace("list");
    }

    function onChangeText(str: string) {
      params.keyWord = str;
    }

    function onFilterData(data: any) {
      console.log(data);
      params.maxCost = data.maxCost;
      params.minCost = data.minCost;
      params.sortType = data.id1;
      params.orgType = data.id2;
      params.orgFeature = data.id4;
      queryOrgMap(params); // 
    }

    // 
    onMounted(() => {
      queryOrgMap(params); // 
      amap = new AMapAPI("mapContainer", 7, pressMarker);
      amap.listener(updateMap, updateMap);
    });

    onUnmounted(() => {
      // 
    });

    return {
      markerData,
      showFlat,
      onPressList,
      onChangeText,
      onFilterData,
      gotoDetail,
    };
  },
});
