
/* eslint-disable */
import { defineComponent, onMounted, ref } from "vue";
import { getMapOption } from "@/lib/echarts/HomeMap";
import { queryOption, queryOrgList } from "@/modules/apis/index";
import { consts } from "@/modules/consts/index";
import Navigate from "@/utils/navigate";

export default defineComponent({
  name: "Home",
  setup() {
    const router = Navigate.get();
    const tabIndex = ref(0);
    const tabs = ref<Array<any>>([]);
    const itemList = ref<Array<any>>([]);
    let params = {
      keyWord: "",
      districtId: consts.city.id,
      sortType: "1",
      orgType: "",
      orgFeature: "",
      minCost: "",
      maxCost: "",
      userLon: consts.city.lng,
      userLat: consts.city.lat,
      pageNum: 1,
      pageSize: 10,
    };

    function gotoOrgList() {
      router.push("list");
    }

    function gotoDetail() {
      router.push("vrview");
    }

    function onTabChange(id: number) {
      tabIndex.value = id;
      queryList();
    }

    async function getOption() {
      const { data, status } = await queryOption();
      if (status) {
        consts.orgTags = data.orgFeatures;
        consts.orgTypes = data.orgTypes;
        consts.costTypes = data.costTypes;
        tabs.value = data.orgTypes;
        tabIndex.value = data.orgTypes[0].id;
        queryList();
      }
    }

    async function queryList() {
      params.orgType = tabIndex.value + "";
      const { data, status } = await queryOrgList(params);
      if (status) {
        itemList.value = data.content;
      }
    }

    // 
    onMounted(() => {
      const initData = [
        {name: ''},
        {name: ''},
        {name: ''},
        {name: ''},
        {name: ''},
        {name: ''}
      ]
      getOption();
      getMapOption('', initData);
    });

    return {
      tabs,
      tabIndex,
      itemList,
      gotoOrgList,
      gotoDetail,
      onTabChange,
    };
  },
});
