<template>
  <!--  -->
  <div class="container">
    <div class="start">
      <section class="start-top">
        <div class="start-top-bg">
          <div class="start-top-box">
            <div class="start-top-title">
              <span></span>
            </div>
            <div id="echarts-map" />
            <a class="start-top-btn" @click="gotoOrgList"></a>
          </div>
        </div>
      </section>
    </div>
    <div class="tab-container">
      <div class="tab-title">
        <span></span>
      </div>
      <div class="tab-box">
        <div
          :class="'tab-text' + (tabIndex === e.id ? '2' : '')"
          v-for="e in tabs"
          :key="e.id"
          @click="onTabChange(e.id)"
        >
          {{ e.typeName }}
        </div>
      </div>
      <div class="item-container">
        <div class="item-box" v-for="item in itemList" :key="item.id" @click="gotoDetail">
          <div style="position: relative">
            <img
              class="item-img"
              src="http://tpc.googlesyndication.com/simgad/5843493769827749134"
            />
            <div class="item-address">
              {{ item.addressName1 }}{{ item.addressName2 }}
            </div>
          </div>
          <div class="item-title">{{ item.orgName }}</div>
          <div class="tag-box">
            <span
              v-for="tag in item.orgFeatures || []"
              :key="tag"
              class="item-tag"
              >{{ tag }}</span
            >
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
/* eslint-disable */
import { defineComponent, onMounted, ref } from "vue";
import { getMapOption } from "@/lib/echarts/HomeMap";
import { queryOption, queryOrgList } from "@/modules/apis/index";
import { consts } from "@/modules/consts/index";
import Navigate from "@/utils/navigate";

export default defineComponent({
  name: "Home",
  setup() {
    const router = Navigate.get();
    const tabIndex = ref(0);
    const tabs = ref<Array<any>>([]);
    const itemList = ref<Array<any>>([]);
    let params = {
      keyWord: "",
      districtId: consts.city.id,
      sortType: "1",
      orgType: "",
      orgFeature: "",
      minCost: "",
      maxCost: "",
      userLon: consts.city.lng,
      userLat: consts.city.lat,
      pageNum: 1,
      pageSize: 10,
    };

    function gotoOrgList() {
      router.push("list");
    }

    function gotoDetail() {
      router.push("vrview");
    }

    function onTabChange(id: number) {
      tabIndex.value = id;
      queryList();
    }

    async function getOption() {
      const { data, status } = await queryOption();
      if (status) {
        consts.orgTags = data.orgFeatures;
        consts.orgTypes = data.orgTypes;
        consts.costTypes = data.costTypes;
        tabs.value = data.orgTypes;
        tabIndex.value = data.orgTypes[0].id;
        queryList();
      }
    }

    async function queryList() {
      params.orgType = tabIndex.value + "";
      const { data, status } = await queryOrgList(params);
      if (status) {
        itemList.value = data.content;
      }
    }

    // 
    onMounted(() => {
      const initData = [
        {name: ''},
        {name: ''},
        {name: ''},
        {name: ''},
        {name: ''},
        {name: ''}
      ]
      getOption();
      getMapOption('', initData);
    });

    return {
      tabs,
      tabIndex,
      itemList,
      gotoOrgList,
      gotoDetail,
      onTabChange,
    };
  },
});
</script>
