
/* eslint-disable */
import { defineComponent, ref } from "vue";
import { consts } from "@/modules/consts/index";

const _def = {
  id1: 1,
  id2: 0,
  id3: -1,
  id4: -1,
  minCost: "",
  maxCost: "",
};
export default defineComponent({
  name: "FilterBar",
  props: {
    onChange: Function,
  },
  setup(props) {
    const tabs = [
      { name: "" },
      { name: "" },
      { name: "" },
      { name: "" },
    ];
    const tabId = ref(-1);
    const tab1List = [
      { id: 1, typeName: "" },
      { id: 2, typeName: "" },
      { id: 3, typeName: "" },
      { id: 4, typeName: "" },
    ];
    const tab2List = consts.orgTypes;
    const tab3List = consts.costTypes;
    const tab4List = consts.orgTags;

    const tabData = ref(_def);

    function onTabChange(inx: number) {
      tabId.value = inx;
      tabData.value = _def;
    }

    function onCancel() {
      tabId.value = -1;
    }

    // 
    function onComfirm() {
      tabId.value = -1;
      const valu = tabData.value;

      let minCost = valu.minCost ? valu.minCost : 0;
      let maxCost = valu.maxCost ? valu.maxCost : 10000000;
      if (valu.minCost || valu.maxCost) {
        valu.id3 = -1;
      } else if (valu.id3 > -1) {
        tab3List.forEach((e: any) => {
          if (e.id === valu.id3) {
            const prices = e.typeName.split("-");
            minCost = prices[0];
            maxCost = prices[1];
          }
        });
      }
      const data: any = {
        id1: valu.id1,
        id2: valu.id2 < 1 ? "" : valu.id2,
        // id3: valu.id3 < 1 ? "" : valu.id3,
        id4: valu.id4 < 1 ? "" : valu.id4,
        minCost,
        maxCost,
      };
      if (props.onChange) {
        props.onChange(data);
      }
    }

    function onPress(index: number, id: number) {
      switch (index) {
        case 1:
          tabData.value.id1 = id;
          break;
        case 2:
          tabData.value.id2 = id;
          break;
        case 3:
          tabData.value.id3 = id;
          break;
        default:
          tabData.value.id4 = id;
          break;
      }
      if (index !== 3) {
        onComfirm();
      }
    }

    return {
      tabs,
      tabId,
      tabData,
      tab1List,
      tab2List,
      tab3List,
      tab4List,
      onCancel,
      onComfirm,
      onPress,
      onTabChange,
    };
  },
});
