/* eslint-disable */
import * as Three from 'three';
import { OrbitControl } from './OrbitControl';

let camera: Three.PerspectiveCamera;
let controls: OrbitControl;
let renderer: Three.WebGLRenderer;
let scene: Three.Scene;

export function newScene(body: any) {

  // const container = document.getElementById('container');

  renderer = new Three.WebGLRenderer();
  renderer.setPixelRatio(window.devicePixelRatio);
  renderer.setSize(window.innerWidth, window.innerHeight);
  body.appendChild(renderer.domElement);

  scene = new Three.Scene();

  camera = new Three.PerspectiveCamera(90, window.innerWidth / window.innerHeight, 0.1, 100);
  camera.position.z = 0.01;

  controls = new OrbitControl(camera, renderer.domElement);
  
  const textures = getTexturesFromAtlasFile("static/img/house_3d.jpg", 6);

  const materials = [];

  for (let i = 0; i < 6; i++) {

    materials.push(new Three.MeshBasicMaterial({ map: textures[i] }));

  }

  const skyBox = new Three.Mesh(new Three.BoxBufferGeometry(1, 1, 1), materials);
  skyBox.geometry.scale(1, 1, - 1);
  scene.add(skyBox);

  window.addEventListener('resize', onWindowResize, false);

  animate();
}

function getTexturesFromAtlasFile(atlasImgUrl: string, tilesNum: number) {

  const textures: any = [];

  for (let i = 0; i < tilesNum; i++) {

    textures[i] = new Three.Texture();

  }

  const imageObj = new Image();

  imageObj.onload = function () {

    let canvas: HTMLCanvasElement;
    let context: CanvasRenderingContext2D | null;
    const tileWidth = imageObj.height;

    for (let i = 0; i < textures.length; i++) {

      canvas = document.createElement('canvas');
      context = canvas.getContext('2d');
      canvas.height = tileWidth;
      canvas.width = tileWidth;
      context?.drawImage(imageObj, tileWidth * i, 0, tileWidth, tileWidth, 0, 0, tileWidth, tileWidth);
      textures[i].image = canvas;
      textures[i].needsUpdate = true;

    }

  };

  imageObj.src = atlasImgUrl;

  return textures;

}

function onWindowResize() {

  camera.aspect = window.innerWidth / window.innerHeight;
  camera.updateProjectionMatrix();

  renderer.setSize(window.innerWidth, window.innerHeight);

}

function animate() {

  requestAnimationFrame(animate);

  controls.update(); // required when damping is enabled

  renderer.render(scene, camera);

}