export function formatDate(): number {
  return Date.now();
}

let lastTime = 0;
export function filterClick(): boolean {
  if(Date.now() - lastTime > 1000) {
    lastTime = Date.now();
    return true;
  }
  return false;
}

export function formatDistance(num: number): string {
  if(num > 1000) {
    return Math.round(num/100)/10+'';
  }
  return num+'';
}
