
/* eslint-disable */
import { defineComponent, onMounted } from "vue";

export default defineComponent({
  name: "ListView",
  props: {
    // items: Array,
    onRefresh: Function,
    onLoading: Function,
  },
  setup(props) {
    const interval = 90;
    let statY = 0;
    let endY = 0;
    let isTop = true;
    let isBottom = false;
    let lastY = 0;

    function onPullDown() {
      if (props.onRefresh) {
        props.onRefresh();
      }
      console.log("=======>onPullDown");
    }

    function onLoadUp() {
      if (props.onLoading) {
        props.onLoading();
      }
      console.log("=======>onLoadUp");
    }

    function onStartTouch(e: any) {
      statY = e.touches[0].pageY;
      // console.log(e.touches[0]);
    }

    function onMoveTouch(e: any) {
      endY = e.touches[0].pageY;
      // console.log(e.touches);
    }

    function onEndTouch() {
      if (Math.abs(endY - statY) > interval) {
        if (isTop) {
          onPullDown();
        } else if (isBottom) {
          onLoadUp();
        }
      }
    }

    function onScroll() {
      const body = document.getElementById("listView321");
      if (body) {
        body.addEventListener("scroll", () => {
          const movePx = body.scrollTop;
          const divHeight = movePx + body.offsetHeight;
          isTop = movePx === 0;
          isBottom = body.scrollHeight === divHeight;
          // if (movePx - lastY > interval) {
          //   lastY = movePx;
          // } else if (lastY - movePx > interval) {
          //   lastY = movePx;
          // }
          // console.log("-------->",body.offsetTop, divHeight, body.scrollHeight);
        });
        body.addEventListener("touchstart", onStartTouch, false);
        body.addEventListener("touchend", onEndTouch, false);
        body.addEventListener("touchmove", onMoveTouch, false);
      }
    }

    onMounted(() => {
      onScroll();
    });
    // return {};
  },
});
