/* eslint-disable */
import {
  EventDispatcher,
  MOUSE,
  Quaternion,
  Spherical,
  TOUCH,
  Vector2,
  Vector3
} from 'three';

const mouseButtons = { LEFT: MOUSE.ROTATE, MIDDLE: MOUSE.DOLLY, RIGHT: MOUSE.PAN };
const touches = { ONE: TOUCH.ROTATE, TWO: TOUCH.DOLLY_PAN };
const keys = { LEFT: 37, UP: 38, RIGHT: 39, BOTTOM: 40 };

let screenSpacePanning = false; // pan orthogonal to world-space direction camera.up
let object: any = {};
let domElement: any= {};

// Set to false to disable this control
let enabled = true;

// "target" sets the location of focus, where the object orbits around
let target = new Vector3();

// How far you can dolly in and out ( PerspectiveCamera only )
let minDistance = 0;
let maxDistance = Infinity;

// How far you can zoom in and out ( OrthographicCamera only )
let minZoom = 0;
let maxZoom = Infinity;

// How far you can orbit vertically, upper and lower limits.
// Range is 0 to Math.PI radians.
let minPolarAngle = 0; // radians
let maxPolarAngle = Math.PI; // radians

// How far you can orbit horizontally, upper and lower limits.
// If set, the interval [ min, max ] must be a sub-interval of [ - 2 PI, 2 PI ], with ( max - min < 2 PI )
let minAzimuthAngle = - Infinity; // radians
let maxAzimuthAngle = Infinity; // radians

// Set to true to enable damping (inertia)
// If damping is enabled, you must call controls.update() in your animation loop
let enableDamping = false;
let dampingFactor = 0.05;

// This option actually enables dollying in and out; left as "zoom" for backwards compatibility.
// Set to false to disable zooming
let enableZoom = true;
let zoomSpeed = 1.0;

// Set to false to disable rotating
let enableRotate = true;
let rotateSpeed = 1.0;

// Set to false to disable panning
let enablePan = true;
let panSpeed = 1.0;
let keyPanSpeed = 7.0;	// pixels moved per arrow key push

// Set to true to automatically rotate around the target
// If auto-rotate is enabled, you must call controls.update() in your animation loop
let autoRotate = false;
let autoRotateSpeed = 2.0; // 30 seconds per round when fps is 60
// Set to false to disable use of the keys
let enableKeys = true;

// for reset
let target0 = target.clone();
let position0 = object.position;
let zoom0 = object.zoom;

let scope = this;

let changeEvent = { type: 'change' };
let startEvent = { type: 'start' };
let endEvent = { type: 'end' };

let STATE = {
  NONE: - 1,
  ROTATE: 0,
  DOLLY: 1,
  PAN: 2,
  TOUCH_ROTATE: 3,
  TOUCH_PAN: 4,
  TOUCH_DOLLY_PAN: 5,
  TOUCH_DOLLY_ROTATE: 6
};


let state = STATE.NONE;

let EPS = 0.000001;

// current position in spherical coordinates
let spherical = new Spherical();
let sphericalDelta = new Spherical();

let scale = 1;
let panOffset = new Vector3();
let zoomChanged = false;

let rotateStart = new Vector2();
let rotateEnd = new Vector2();
let rotateDelta = new Vector2();

let panStart = new Vector2();
let panEnd = new Vector2();
let panDelta = new Vector2();

let dollyStart = new Vector2();
let dollyEnd = new Vector2();
let dollyDelta = new Vector2();




class OrbitControl extends EventDispatcher {
  constructor(obj: any, dom: any) {
    super();
    object = obj;
    domElement = dom;
    this.init();
  }

  init() {
    //
    domElement.addEventListener('contextmenu', this.onContextMenu, false);

    domElement.addEventListener('pointerdown', this.onPointerDown, false);
    domElement.addEventListener('wheel', this.onMouseWheel, false);

    domElement.addEventListener('touchstart', this.onTouchStart, false);
    domElement.addEventListener('touchend', this.onTouchEnd, false);
    domElement.addEventListener('touchmove', this.onTouchMove, false);

    domElement.addEventListener('keydown', this.onKeyDown, false);

    this.update();

    enableZoom = false;
    enablePan = false;
    enableDamping = true;
    rotateSpeed = - 0.25;
  }

  getPolarAngle() {
    return spherical.phi;
  };

  getAzimuthalAngle() {

    return spherical.theta;

  };

  saveState() {

    target0.copy(target);
    position0.copy(object.position);
    zoom0 = object.zoom;
  };

  reset() {
    target.copy(target0);
    object.position.copy(position0);
    object.zoom = zoom0;

    object.updateProjectionMatrix();
    this.dispatchEvent(changeEvent);

    this.update();

    state = STATE.NONE;
  };

  // this method is exposed, but perhaps it would be better if we can make it private...
  update() {

    let offset = new Vector3();

    // so camera.up is the orbit axis
    let quat = new Quaternion().setFromUnitVectors(object.up, new Vector3(0, 1, 0));
    let quatInverse = quat.clone().invert();

    let lastPosition = new Vector3();
    let lastQuaternion = new Quaternion();

    let twoPI = 2 * Math.PI;


      let position = object.position;

      offset.copy(position).sub(target);

      // rotate offset to "y-axis-is-up" space
      offset.applyQuaternion(quat);

      // angle from z-axis around y-axis
      spherical.setFromVector3(offset);

      if (autoRotate && state === STATE.NONE) {
        this.rotateLeft(this.getAutoRotationAngle());
      }

      if (enableDamping) {
        spherical.theta += sphericalDelta.theta * dampingFactor;
        spherical.phi += sphericalDelta.phi * dampingFactor;
      } else {
        spherical.theta += sphericalDelta.theta;
        spherical.phi += sphericalDelta.phi;
      }

      // restrict theta to be between desired limits

      let min = minAzimuthAngle;
      let max = maxAzimuthAngle;

      if (isFinite(min) && isFinite(max)) {

        if (min < - Math.PI) min += twoPI; else if (min > Math.PI) min -= twoPI;

        if (max < - Math.PI) max += twoPI; else if (max > Math.PI) max -= twoPI;

        if (min <= max) {
          spherical.theta = Math.max(min, Math.min(max, spherical.theta));
        } else {
          spherical.theta = (spherical.theta > (min + max) / 2) ?
            Math.max(min, spherical.theta) :
            Math.min(max, spherical.theta);
        }
      }

      // restrict phi to be between desired limits
      spherical.phi = Math.max(minPolarAngle, Math.min(maxPolarAngle, spherical.phi));

      spherical.makeSafe();

      spherical.radius *= scale;

      // restrict radius to be between desired limits
      spherical.radius = Math.max(minDistance, Math.min(maxDistance, spherical.radius));

      // move target to panned location

      if (enableDamping === true) {
        target.addScaledVector(panOffset, dampingFactor);
      } else {
        target.add(panOffset);
      }

      offset.setFromSpherical(spherical);

      // rotate offset back to "camera-up-vector-is-up" space
      offset.applyQuaternion(quatInverse);

      position.copy(target).add(offset);

      object.lookAt(target);

      if (enableDamping === true) {
        sphericalDelta.theta *= (1 - dampingFactor);
        sphericalDelta.phi *= (1 - dampingFactor);

        panOffset.multiplyScalar(1 - dampingFactor);
      } else {
        sphericalDelta.set(0, 0, 0);

        panOffset.set(0, 0, 0);
      }
      scale = 1;

      // update condition is:
      // min(camera displacement, camera rotation in radians)^2 > EPS
      // using small-angle approximation cos(x/2) = 1 - x^2 / 8

      if (zoomChanged ||
        lastPosition.distanceToSquared(object.position) > EPS ||
        8 * (1 - lastQuaternion.dot(object.quaternion)) > EPS) {

        this.dispatchEvent(changeEvent);

        lastPosition.copy(object.position);
        lastQuaternion.copy(object.quaternion);
        zoomChanged = false;
        return true;
      }
      return false;
  };

  dispose() {

    domElement.removeEventListener('contextmenu', this.onContextMenu, false);

    domElement.removeEventListener('pointerdown', this.onPointerDown, false);
    domElement.removeEventListener('wheel', this.onMouseWheel, false);

    domElement.removeEventListener('touchstart', this.onTouchStart, false);
    domElement.removeEventListener('touchend', this.onTouchEnd, false);
    domElement.removeEventListener('touchmove', this.onTouchMove, false);

    domElement.ownerDocument.removeEventListener('pointermove', this.onPointerMove, false);
    domElement.ownerDocument.removeEventListener('pointerup', this.onPointerUp, false);

    domElement.removeEventListener('keydown', this.onKeyDown, false);
  };

  getAutoRotationAngle() {
    return 2 * Math.PI / 60 / 60 * autoRotateSpeed;
  }

  getZoomScale() {
    return Math.pow(0.95, zoomSpeed);
  }

  rotateLeft(angle: number) {
    sphericalDelta.theta -= angle;
  }

  rotateUp(angle: number) {
    sphericalDelta.phi -= angle;
  }


  panLeft(distance: number, objectMatrix: any) {
    const v = new Vector3();
    v.setFromMatrixColumn(objectMatrix, 0); // get X column of objectMatrix
    v.multiplyScalar(-distance);
    panOffset.add(v);
  };

  panUp(distance: number, objectMatrix: any) {
    const v = new Vector3();

    if (screenSpacePanning === true) {
      v.setFromMatrixColumn(objectMatrix, 1);
    } else {
      v.setFromMatrixColumn(objectMatrix, 0);
      v.crossVectors(object.up, v);
    }
    v.multiplyScalar(distance);
    panOffset.add(v);
  };

  pan(deltaX: number, deltaY: number) {

    let offset = new Vector3();
    let element = domElement;

    if (object.isPerspectiveCamera) {
      // perspective
      let position = object.position;
      offset.copy(position).sub(target);
      let targetDistance = offset.length();

      // half of the fov is center to top of screen
      targetDistance *= Math.tan((object.fov / 2) * Math.PI / 180.0);

      // we use only clientHeight here so aspect ratio does not distort speed
      this.panLeft(2 * deltaX * targetDistance / element.clientHeight, object.matrix);
      this.panUp(2 * deltaY * targetDistance / element.clientHeight, object.matrix);
    } else if (object.isOrthographicCamera) {
      // orthographic
      this.panLeft(deltaX * (object.right - object.left) / object.zoom / element.clientWidth, object.matrix);
      this.panUp(deltaY * (object.top - object.bottom) / object.zoom / element.clientHeight, object.matrix);
    } else {
      // camera neither orthographic nor perspective
      console.warn('WARNING: OrbitControls.js encountered an unknown camera type - pan disabled.');
      enablePan = false;
    }
  }

  dollyOut(dollyScale: number) {

    if (object.isPerspectiveCamera) {
      scale /= dollyScale;
    } else if (object.isOrthographicCamera) {
      object.zoom = Math.max(minZoom, Math.min(maxZoom, object.zoom * dollyScale));
      object.updateProjectionMatrix();
      zoomChanged = true;
    } else {
      console.warn('WARNING: OrbitControls.js encountered an unknown camera type - dolly/zoom disabled.');
      enableZoom = false;
    }
  }

  dollyIn(dollyScale: number) {

    if (object.isPerspectiveCamera) {
      scale *= dollyScale;
    } else if (object.isOrthographicCamera) {
      object.zoom = Math.max(minZoom, Math.min(maxZoom, object.zoom / dollyScale));
      object.updateProjectionMatrix();
      zoomChanged = true;
    } else {
      console.warn('WARNING: OrbitControls.js encountered an unknown camera type - dolly/zoom disabled.');
      enableZoom = false;
    }
  }

  handleMouseDownRotate = (event: any) => {
    rotateStart.set(event.clientX, event.clientY);
  }

  handleMouseDownDolly = (event: any) => {
    dollyStart.set(event.clientX, event.clientY);
  }

  handleMouseDownPan = (event: any) => {
    panStart.set(event.clientX, event.clientY);
  }

  handleMouseMoveRotate = (event: any) => {

    rotateEnd.set(event.clientX, event.clientY);
    rotateDelta.subVectors(rotateEnd, rotateStart).multiplyScalar(rotateSpeed);
    let element = domElement;
    this.rotateLeft(2 * Math.PI * rotateDelta.x / element.clientHeight); // yes, height
    this.rotateUp(2 * Math.PI * rotateDelta.y / element.clientHeight);
    rotateStart.copy(rotateEnd);
    this.update();
  }

  handleMouseMoveDolly = (event: any) => {

    dollyEnd.set(event.clientX, event.clientY);

    dollyDelta.subVectors(dollyEnd, dollyStart);

    if (dollyDelta.y > 0) {
      this.dollyOut(this.getZoomScale());
    } else if (dollyDelta.y < 0) {
      this.dollyIn(this.getZoomScale());
    }
    dollyStart.copy(dollyEnd);
    this.update();
  }

  handleMouseMovePan = (event: any) => {
    panEnd.set(event.clientX, event.clientY);
    panDelta.subVectors(panEnd, panStart).multiplyScalar(panSpeed);
    this.pan(panDelta.x, panDelta.y);

    panStart.copy(panEnd);
    this.update();
  }

  handleMouseUp(e: any) {

  }

  handleMouseWheel = (event: any) => {

    if (event.deltaY < 0) {
      this.dollyIn(this.getZoomScale());
    } else if (event.deltaY > 0) {
      this.dollyOut(this.getZoomScale());
    }
    this.update();
  }

  handleKeyDown = (event: any) => {

    let needsUpdate = false;
    switch (event.keyCode) {
      case keys.UP:
        this.pan(0, keyPanSpeed);
        needsUpdate = true;
        break;
      case keys.BOTTOM:
        this.pan(0, - keyPanSpeed);
        needsUpdate = true;
        break;
      case keys.LEFT:
        this.pan(keyPanSpeed, 0);
        needsUpdate = true;
        break;
      case keys.RIGHT:
        this.pan(- keyPanSpeed, 0);
        needsUpdate = true;
        break;
    }
    if (needsUpdate) {
      // prevent the browser from scrolling on cursor keys
      event.preventDefault();
      this.update();
    }
  }

  handleTouchStartRotate = (event: any) => {

    if (event.touches.length == 1) {
      rotateStart.set(event.touches[0].pageX, event.touches[0].pageY);
    } else {
      let x = 0.5 * (event.touches[0].pageX + event.touches[1].pageX);
      let y = 0.5 * (event.touches[0].pageY + event.touches[1].pageY);
      rotateStart.set(x, y);
    }
  }

  handleTouchStartPan = (event: any) => {

    if (event.touches.length == 1) {
      panStart.set(event.touches[0].pageX, event.touches[0].pageY);
    } else {
      let x = 0.5 * (event.touches[0].pageX + event.touches[1].pageX);
      let y = 0.5 * (event.touches[0].pageY + event.touches[1].pageY);
      panStart.set(x, y);
    }
  }

  handleTouchStartDolly = (event: any) => {

    let dx = event.touches[0].pageX - event.touches[1].pageX;
    let dy = event.touches[0].pageY - event.touches[1].pageY;

    let distance = Math.sqrt(dx * dx + dy * dy);
    dollyStart.set(0, distance);
  }

  handleTouchStartDollyPan = (event: any) => {
    if (enableZoom) this.handleTouchStartDolly(event);

    if (enablePan) this.handleTouchStartPan(event);
  }

  handleTouchStartDollyRotate = (event: any) => {

    if (enableZoom) this.handleTouchStartDolly(event);

    if (enableRotate) this.handleTouchStartRotate(event);
  }

  handleTouchMoveRotate = (event: any) => {

    if (event.touches.length == 1) {
      rotateEnd.set(event.touches[0].pageX, event.touches[0].pageY);
    } else {
      let x = 0.5 * (event.touches[0].pageX + event.touches[1].pageX);
      let y = 0.5 * (event.touches[0].pageY + event.touches[1].pageY);
      rotateEnd.set(x, y);
    }

    rotateDelta.subVectors(rotateEnd, rotateStart).multiplyScalar(rotateSpeed);

    let element = domElement;
    this.rotateLeft(2 * Math.PI * rotateDelta.x / element.clientHeight); // yes, height
    this.rotateUp(2 * Math.PI * rotateDelta.y / element.clientHeight);

    rotateStart.copy(rotateEnd);
  }

  handleTouchMovePan = (event: any) => {

    if (event.touches.length == 1) {
      panEnd.set(event.touches[0].pageX, event.touches[0].pageY);
    } else {
      let x = 0.5 * (event.touches[0].pageX + event.touches[1].pageX);
      let y = 0.5 * (event.touches[0].pageY + event.touches[1].pageY);
      panEnd.set(x, y);
    }
    panDelta.subVectors(panEnd, panStart).multiplyScalar(panSpeed);
    this.pan(panDelta.x, panDelta.y);
    panStart.copy(panEnd);
  }

  handleTouchMoveDolly = (event: any) => {

    let dx = event.touches[0].pageX - event.touches[1].pageX;
    let dy = event.touches[0].pageY - event.touches[1].pageY;
    let distance = Math.sqrt(dx * dx + dy * dy);

    dollyEnd.set(0, distance);
    dollyDelta.set(0, Math.pow(dollyEnd.y / dollyStart.y, zoomSpeed));
    this.dollyOut(dollyDelta.y);
    dollyStart.copy(dollyEnd);
  }

  handleTouchMoveDollyPan = (event: any) => {
    if (enableZoom) this.handleTouchMoveDolly(event);

    if (enablePan) this.handleTouchMovePan(event);
  }

  handleTouchMoveDollyRotate = (event: any) => {

    if (enableZoom) this.handleTouchMoveDolly(event);

    if (enableRotate) this.handleTouchMoveRotate(event);
  }

  handleTouchEnd = (event: any) => {

  }

  onPointerDown = (event: any) => {

    if (enabled === false) return;

    switch (event.pointerType) {
      case 'mouse':
      case 'pen':
        this.onMouseDown(event);
        break;
    }
  }

  onPointerMove = (event: any) => {
    if (enabled === false) return;

    switch (event.pointerType) {
      case 'mouse':
      case 'pen':
        this.onMouseMove(event);
        break;
    }
  }

  onPointerUp = (event: any) => {

    switch (event.pointerType) {
      case 'mouse':
      case 'pen':
        this.onMouseUp(event);
        break;
    }
  }

  onMouseDown = (event: any) => {
    // Prevent the browser from scrolling.
    event.preventDefault();
    // Manually set the focus since calling preventDefault above
    // prevents the browser from setting it automatically.
    domElement.focus ? domElement.focus() : window.focus();

    let mouseAction;
    switch (event.button) {
      case 0:
        mouseAction = mouseButtons.LEFT;
        break;
      case 1:
        mouseAction = mouseButtons.MIDDLE;
        break;
      case 2:
        mouseAction = mouseButtons.RIGHT;
        break;
      default:
        mouseAction = - 1;
    }

    switch (mouseAction) {
      case MOUSE.DOLLY:
        if (enableZoom === false) return;
        this.handleMouseDownDolly(event);
        state = STATE.DOLLY;
        break;
      case MOUSE.ROTATE:
        if (event.ctrlKey || event.metaKey || event.shiftKey) {
          if (enablePan === false) return;
          this.handleMouseDownPan(event);
          state = STATE.PAN;
        } else {
          if (enableRotate === false) return;
          this.handleMouseDownRotate(event);
          state = STATE.ROTATE;
        }
        break;
      case MOUSE.PAN:
        if (event.ctrlKey || event.metaKey || event.shiftKey) {
          if (enableRotate === false) return;
          this.handleMouseDownRotate(event);
          state = STATE.ROTATE;
        } else {
          if (enablePan === false) return;
          this.handleMouseDownPan(event);
          state = STATE.PAN;
        }
        break;
      default:
        state = STATE.NONE;
    }

    if (state !== STATE.NONE) {
      domElement.ownerDocument.addEventListener('pointermove', this.onPointerMove, false);
      domElement.ownerDocument.addEventListener('pointerup', this.onPointerUp, false);

      this.dispatchEvent(startEvent);
    }
  }

  onMouseMove = (event: any) => {

    if (enabled === false) return;

    event.preventDefault();
    switch (state) {
      case STATE.ROTATE:
        if (enableRotate === false) return;
        this.handleMouseMoveRotate(event);
        break;
      case STATE.DOLLY:
        if (enableZoom === false) return;
        this.handleMouseMoveDolly(event);
        break;
      case STATE.PAN:
        if (enablePan === false) return;
        this.handleMouseMovePan(event);
        break;
    }

  }

  onMouseUp = (event: any) => {

    domElement.ownerDocument.removeEventListener('pointermove', this.onPointerMove, false);
    domElement.ownerDocument.removeEventListener('pointerup', this.onPointerUp, false);

    if (enabled === false) return;

    this.handleMouseUp(event);
    this.dispatchEvent(endEvent);
    state = STATE.NONE;
  }

  onMouseWheel = (event: any) => {
    if (enabled === false || enableZoom === false || (state !== STATE.NONE && state !== STATE.ROTATE)) return;

    event.preventDefault();
    event.stopPropagation();
    this.dispatchEvent(startEvent);
    this.handleMouseWheel(event);
    this.dispatchEvent(endEvent);
  }

  onKeyDown = (event: any) => {

    if (enabled === false || enableKeys === false || enablePan === false) return;
    this.handleKeyDown(event);
  }

  onTouchStart = (event: any) => {
    if (enabled === false) return;

    event.preventDefault(); // prevent scrolling
    switch (event.touches.length) {
      case 1:
        switch (touches.ONE) {
          case TOUCH.ROTATE:
            if (enableRotate === false) return;
            this.handleTouchStartRotate(event);
            state = STATE.TOUCH_ROTATE;
            break;
          case TOUCH.PAN:
            if (enablePan === false) return;
            this.handleTouchStartPan(event);
            state = STATE.TOUCH_PAN;
            break;
          default:
            state = STATE.NONE;
        }
        break;
      case 2:
        switch (touches.TWO) {
          case TOUCH.DOLLY_PAN:
            if (enableZoom === false && enablePan === false) return;
            this.handleTouchStartDollyPan(event);
            state = STATE.TOUCH_DOLLY_PAN;
            break;
          case TOUCH.DOLLY_ROTATE:
            if (enableZoom === false && enableRotate === false) return;
            this.handleTouchStartDollyRotate(event);
            state = STATE.TOUCH_DOLLY_ROTATE;
            break;
          default:
            state = STATE.NONE;
        }
        break;
      default:
        state = STATE.NONE;
    }
    if (state !== STATE.NONE) {
      this.dispatchEvent(startEvent);
    }

  }

  onTouchMove = (event: any) => {

    if (enabled === false) return;

    event.preventDefault(); // prevent scrolling
    event.stopPropagation();
    switch (state) {
      case STATE.TOUCH_ROTATE:
        if (enableRotate === false) return;
        this.handleTouchMoveRotate(event);
        this.update();
        break;
      case STATE.TOUCH_PAN:
        if (enablePan === false) return;
        this.handleTouchMovePan(event);
        this.update();
        break;
      case STATE.TOUCH_DOLLY_PAN:
        if (enableZoom === false && enablePan === false) return;
        this.handleTouchMoveDollyPan(event);
        this.update();
        break;
      case STATE.TOUCH_DOLLY_ROTATE:
        if (enableZoom === false && enableRotate === false) return;
        this.handleTouchMoveDollyRotate(event);
        this.update();
        break;
      default:
        state = STATE.NONE;
    }

  }

  onTouchEnd = (event: any) => {

    if (enabled === false) return;
    this.handleTouchEnd(event);
    this.dispatchEvent(endEvent);
    state = STATE.NONE;
  }

  onContextMenu = (event: any) => {
    if (enabled === false) return;
    event.preventDefault();
  }

}

// 
class MapControls extends EventDispatcher {

  constructor(object: any, domElement: any) {
    super();
    new OrbitControl(object, domElement);
    this.init();
  }

  init() {
    screenSpacePanning = false;

    mouseButtons.LEFT = MOUSE.PAN;
    mouseButtons.RIGHT = MOUSE.ROTATE;

    touches.ONE = TOUCH.PAN;
    touches.TWO = TOUCH.DOLLY_ROTATE;
  }
};

export { OrbitControl, MapControls };
