<template>
  <!--  -->
  <div class="container">
    <div class="search-box">
      <div class="input-box">
        <img class="icon" src="static/icon/search_ic.png" />
        <input
          class="input"
          maxLength="32"
          enterKeyHint="search"
          placeholder="//"
          v-model="inputStr"
          @change="onChangeText"
        />
      </div>
      <div v-if="inputStr.length > 0" class="search-btn" @click="onSearch">
        
      </div>
      <div v-else class="search-btn" @click="onCancel"></div>
    </div>
    <div class="list-box">
      <div
        :class="'item-box' + (index === 0 ? '2' : '')"
        v-for="(item, index) in itemList"
        :key="item.id"
        @click="onPressItem(item)"
      >
        <div class="name">{{item.name}}</div>
        <div class="city">{{item.fartherName||item.name}}</div>
      </div>

      <EmptyView v-if="itemList.length < 1" value="" />
    </div>
  </div>
</template>
<script lang="ts">
/* eslint-disable */
import { defineComponent, ref } from "vue";
import Navigate from "@/utils/navigate";
import { EmptyView } from "@/components/index";
import { queryDistrict } from "@/modules/apis/index";
import { consts } from '@/modules/consts/index';

export default defineComponent({
  name: "SearchView",
  props: {
    text: String,
    placeholder: String,
    onSearch: Function,
  },
  components: {
    EmptyView,
  },
  setup() {
    const router = Navigate.get();

    const inputStr = ref("");
    const itemList = ref<Array<any>>([]);

    async function onSearch() {
      const { status, data } = await queryDistrict(inputStr.value);
      if (status) {
        if (data.length > 0) {
          itemList.value = data;
        }
      }
    }

    function onPressItem(item: any) {
      consts.city.id = item.id;
      consts.city.name = item.name;
      onBack()
    }

    function onCancel() {
      router.replace('citys')
    }

    function onBack() {
      router.back();
    }

    return {
      inputStr,
      itemList,
      onSearch,
      onPressItem,
      onCancel,
      onBack,
    };
  },
});
</script>
